import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import feathers from '../../feathers';
import axios from 'axios';
import immutable from 'object-path-immutable';
import SweetAlert from 'react-bootstrap-sweetalert';
import Select from 'react-select';

class TipoUsuariosForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.match.params.id,
      data: [],
      total: 0,
      formData: {
        nombre: ''
      },
      openSuccess: false
    };
  }

  async componentWillMount() {
    console.log('id', this.state.id);
    var _this = this;

    if (this.state.id != 'form') {
      var findType = await feathers.service('types').find({
        query: {
          _id: _this.state.id
        }
      });

      console.log('Datos encontrados:', findType.data);
      this.setState({
        formData: {
          nombre: findType.data[0].nombre
        }
      });
    }
    this.getOptionsSelect(
      'tipoUsuarios',
      process.env.REACT_APP_URL_API + '/api/types',
      option => {
        return {
          label: option.name,
          value: option.id
        };
      }
    );
  }

  getOptionsSelect = async (name, url, optionCreateFunction) => {
    var response = await axios.get(url);
    var options = [];
    response.data.map(option => {
      options.push(optionCreateFunction(option));
    });
    this.setState({
      optionsSelect: {
        ...this.state.optionsSelect,
        [name]: options
      }
    });
  };

  onChange = (path, value) => {
    var newFormData = immutable.set(this.state.formData, path, value);

    this.setState({
      formData: newFormData
    });
  };

  onAdd = async formData => {
    let _this = this;

    if (this.form.reportValidity()) {
      if (this.state.id == 'form') {
        let userFeather = await feathers
          .service('types')
          .create(_this.state.formData);
        console.log('userFeather', userFeather);
        if (userFeather._id != undefined) {
          window.location.href = '/#/tipos';
        }
      } else {
        let userFeather = await feathers
          .service('types')
          .patch(_this.state.id, _this.state.formData);
        if (userFeather._id != undefined) {
          window.location.href = '/#/tipos';
        }
      }
    }
  };

  onSend = async formData => {
    let _this = this;
    console.log('Enviando...');
  };

  render() {
    var indexNumber = 0;
    var { formData, openSuccess, optionsSelect } = this.state;
    //DONE Paty - Hacer que se pueda crear/editar USUARIO con los datos que están en el modelo de users

    return (
      <div className="animated fadeIn">
        <form ref={ref => (this.form = ref)}>
          <Row>
            <Col xs="12" lg="12">
              <Card>
                <CardBody>
                  <Row>
                    <Col md={12}>
                      <h4>Editar tipo de usuario</h4>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={3}>
                      <div className="form-group">
                        <label htmlFor="reference">Tipo</label>
                        <input
                          type="text"
                          className="form-control"
                          id="user"
                          value={formData.nombre}
                          onChange={event =>
                            this.onChange('nombre', event.target.value)
                          }
                        />
                      </div>
                    </Col>
                  </Row>

                  <Row
                    className="pull-right"
                    style={{
                      marginTop: '5px',
                      paddingRight: '5px'
                    }}
                  >
                    <Col col="12" sm="12" md="12">
                      <Button
                        color="success"
                        onClick={this.onAdd}
                        className="pull-right"
                      >
                        {/* //DONE Paty - Hacer que te envíe a Editar */}
                        Guardar
                      </Button>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </form>
      </div>
    );
  }
}

export default TipoUsuariosForm;
